@use 'hero';
@use 'componentes/infos_bar.scss';
@use 'componentes/buttons.scss';
@use 'event';
@use 'variables';

*{
	margin: 0;
	padding: 	0;
	box-sizing: border-box;
	font-family: 'Roboto', sans-serif;
	color: variables.$textcolor;
	list-style: none;
}

.container {
	max-width: 960px;
	width: 100%;
	margin: 0 auto;
	
	@media (max-width: 640px){
		max-width: 80%;

	}
	@media (max-width: 1024px){
		max-width: 90%;

	}
}